:root {
  --wheel-font: 'Lato', 'Quicksand', sans-serif;
  --wheel-size: 480px;
  --wheel-slice-spacing: 50px;
  --wheel-border-size: 5px;
  --wheel-color: #da3768;
  --neutral-color: white;
  --PI: 3.14159265358979;
  --nb-item: 12;
  --item-nb: 1;
  --selected-item: 1;
  --nb-turn: 240;
  --spinning-duration: 120s;
  --reset-duration: 100s;
}

/* Conteneur de la roue = cercle rose extérieur */
.wheel-container {
  display: block;
  position: relative;
  box-sizing: content-box;
  width: calc(480px + 2 * 5px);
  width: calc(var(--wheel-size) + 2 * var(--wheel-border-size));
  height: calc(480px + 2 * 5px);
  height: calc(var(--wheel-size) + 2 * var(--wheel-border-size));
  padding: 3px;
  margin: auto;
  background-color: white;
  background-color: var(--neutral-color);
  border: solid #da3768 3px;
  border: solid var(--wheel-color) 3px;
  border-radius: 50%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/* Triangle de sélection = 1 rose +  1 blanc */
.wheel-container::before,
.wheel-container::after {
  content: '';
  display: block;
  position: absolute;
  height: 0;
  width: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 2;
  border: solid transparent 20px;
  border-left-width: 0;
}

.wheel-container::before {
  right: 0px;
  border-right-color: #da3768;
  border-right-color: var(--wheel-color);
}

.wheel-container::after {
  right: -5px;
  border-right-color: white;
  border-right-color: var(--neutral-color);
}

/* Roue */
.wheel {
  display: block;
  position: relative;
  box-sizing: content-box;
  margin: auto;
  width: 480px;
  width: var(--wheel-size);
  height: 480px;
  height: var(--wheel-size);
  overflow: hidden;
  border-radius: 50%;
  border: solid #da3768 5px;
  border: solid var(--wheel-color) var(--wheel-border-size);
  background-color: #da3768;
  background-color: var(--wheel-color);
  transition: -webkit-transform 100s;
  transition: transform 100s;
  transition: transform 100s, -webkit-transform 100s;
  transition: -webkit-transform var(--reset-duration);
  transition: transform var(--reset-duration);
  transition: transform var(--reset-duration), -webkit-transform var(--reset-duration);
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  cursor: pointer;
}

.wheel.spinning {
  transition: -webkit-transform 120s;
  transition: transform 120s;
  transition: transform 120s, -webkit-transform 120s;
  transition: -webkit-transform var(--spinning-duration);
  transition: transform var(--spinning-duration);
  transition: transform var(--spinning-duration), -webkit-transform var(--spinning-duration);
  -webkit-transform: rotate(calc(240 * 360deg + (-360deg * 1 / 12)));
          transform: rotate(calc(240 * 360deg + (-360deg * 1 / 12)));
  -webkit-transform: rotate(calc(var(--nb-turn) * 360deg + (-360deg * var(--selected-item) / var(--nb-item, 1))));
          transform: rotate(calc(var(--nb-turn) * 360deg + (-360deg * var(--selected-item) / var(--nb-item, 1))));
}

/* Centre de la roue = rond blanc au centre */
.wheel::after {
  display: block;
  position: absolute;
  content: '';
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  /* background-color: var(--neutral-color); */
  background-color: yellow;
  width: 30px;
  height: 30px;
  z-index: 2;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50%;
}

/* Element sur la roue */
.wheel-item {
  display: block;
  position: absolute;
  box-sizing: border-box;

  /* position de l'item */
  top: 50%;
  left: 50%;
  width: 50%;
  -webkit-transform-origin: center left;
          transform-origin: center left;
  -webkit-transform: translateY(-50%) rotate(calc(1 * (360deg / 12)));
          transform: translateY(-50%) rotate(calc(1 * (360deg / 12)));
  -webkit-transform: translateY(-50%) rotate(calc(var(--item-nb) * (360deg / var(--nb-item, 1))));
          transform: translateY(-50%) rotate(calc(var(--item-nb) * (360deg / var(--nb-item, 1))));

  /* texte */
  color: white;
  color: var(--neutral-color);
  text-align: right;
  padding: 0 25px 0 50px;
  font-family: 'Lato', 'Quicksand', sans-serif;
  font-family: var(--wheel-font);
}

/* Background de l'élément = triangle rose plus clair */
.wheel-item:before {
  content: ' ';
  display: block;
  position: absolute;
  box-sizing: border-box;
  z-index: -1;
  width: 0;
  height: 0;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding-left: 0px;
  opacity: 0.25;

  /* 
    Largeur du triangle
    Je ne sais pas pourquoi j'ai besoin d'ajouter 
      " + var(--wheel-size) / 2 " dans slice-max-width  ==> donne 2.PI.R + R
    mais globalement ca marche a peu près bien 
    TODO: A étudier 
  */
  --slice-max-width: calc(var(--PI) * var(--wheel-size) + var(--wheel-size) / 2);
  --slice-width: calc((var(--slice-max-width) / var(--nb-item)) - var(--wheel-slice-spacing));
  border: solid transparent calc(var(--slice-width) / 2);
  border-left: solid transparent 0;
  /* profondeur du triangle = du centre de la roue à la bordure = 300px / 2 */
  border-right: solid white calc(480px / 2);
  border-right: solid var(--neutral-color) calc(var(--wheel-size) / 2);
}

@media only screen and (max-width: 600px) {
  :root {
    --wheel-font: 'Lato', 'Quicksand', sans-serif;
    --wheel-size: 90vw;
    --wheel-slice-spacing: 50px;
    --wheel-border-size: 5px;
    --wheel-color: #da3768;
    --neutral-color: white;
    --PI: 3.14159265358979;
    --nb-item: 0;
    --item-nb: 0;
    --selected-item: 0;
    --nb-turn: 240;
    --spinning-duration: 120s;
    --reset-duration: 100s;
  }
}

@media only screen and (max-width: 600px) {
  .wheel-item {
    display: block;
    position: absolute;
    box-sizing: border-box;

    /* position de l'item */
    top: 50%;
    left: 50%;
    width: 50%;

    -webkit-transform-origin: center left;

            transform-origin: center left;
    -webkit-transform: translateY(-50%) rotate(calc(1 * (360deg / 12)));
            transform: translateY(-50%) rotate(calc(1 * (360deg / 12)));
    -webkit-transform: translateY(-50%) rotate(calc(var(--item-nb) * (360deg / var(--nb-item, 1))));
            transform: translateY(-50%) rotate(calc(var(--item-nb) * (360deg / var(--nb-item, 1))));

    /* texte */
    font-size: 12px;
    color: white;
    color: var(--neutral-color);
    text-align: right;
    padding: 0 5px 0 5px;
    font-family: 'Lato', 'Quicksand', sans-serif;
    font-family: var(--wheel-font);
  }
}

